.app-name {
    padding: 0 auto;
    text-align: center;
    margin: 0;
}

.app-name h1 {
    font-size: 25px;
    margin: 0 auto;
}

.date-list {
    list-style: none;
    white-space: nowrap;
    display: flex;

    padding: 0;
    margin: 0;
    overflow-x: auto;
    scrollbar-width: none;
    flex-direction: row;
    gap: 10px;
}

.number-of-week {
    font-size: 16px;
}

.date-list-block {
    padding: 10px 8px;

    border-radius: 10px;
}

.date-list-block li {
    font-size: 18px;
}

.day-of-week {

    font-weight: bold;
    font-size: 18px;
}

.main-container {
    padding: 25px 12px 0px 12px;
}

.today {
    text-align: center;
    font-weight: 400;
    font-size: 20px;
}